import contents from '../components/contents.vue'
import Navigation from '../components/Navigation.vue'
import {listDate as contentsDate} from '../db/contents.js'
import listDate from '../db/list.js'
import dateFormat from 'dateformat';

export default {
  components: {
    contents,
    Navigation
  },
  data() {
    return {
      type: this.$route.params.type,
      values: contentsDate.filter(this.filterConditions)
    }
  },
  methods: {
    filterConditions: function(element, index, array) {
      let type = this.$route.params.type;
      let now = new Date();
      let isToday = dateFormat(now, "yyyy-mm-dd") === element.updateDate;
      if (type === 'today') {
        return (!element.isEnd && isToday);
      } else if (type === 'urgent') {
        return (!element.isEnd && element.isUrgent);
      } else if (type === 'end') {
        return (element.isEnd);
      } else if (type === 'long') {
        return (!element.isEnd && element.isLong);
      } else if (type === 'all') {
        return (!element.isEnd);
      } else {
        return (!element.isEnd && element.group.indexOf(type) > -1);
      }
    },
    thisNext: function(url){
      this.$router.push(url)
    }
  },
  watch: {
    '$route' (to, from) {
      this.type = to.params.type;
      this.values = contentsDate.filter(this.filterConditions);
    }
  }
}



